
.element {
  width: calc(100%/3);
}
.info {
  width: 75%;
  margin: 0 auto;
  color: #757575;
}
.info h2 {
  font-family: 'Poppins';
  font-size: 24px;
  color: #212121;
  margin: 15px 0px 5px 0px;
}
.thumb {
  width: 100%;
}
.subinfo div {
  margin: 10px;
  display: inline;
}
.subinfo .stars {
  margin-left: 0;
}
.subinfo .stars i {
  font-size: 10px;
  margin: 0;
}
.subinfo .stars .active {
  color: #de1212;
}
.overview {
  max-height: 100px;
  margin: 15px 0px 50px 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* Fallback for non-webkit */
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}




